.profile-card {
    width: 300px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #f5f5f5;
  }
  
  .profile-header {
    text-align: center;
    margin-bottom: 10px;
  }
  
  .profile-header h2 {
    margin: 0;
    font-size: 20px;
    color: #333;
  }
  
  .profile-body p {
    margin: 0;
    margin-bottom: 5px;
    color: #555;
  }
  